.ui_containimg {
  display: block;
  max-width: 100%;
  max-height: 100%;
  object-fit: contain;
}

.ui_angle_top_left {
  clip-path: polygon(0 0, 0% 100%, 100% 0);
}

.ui_grid_template {
  grid-template: var(--ui_grid_template);
}

.ui_themeColor {
  color: var(--themeColor) !important;
}

.ui_text_gray_hover {
  color: #9ca3af;

  &:hover {
    color: #111827;
  }
}

.ui_text_black_hover {
  color: #555;

  &:hover {
    color: #000;
  }
}

.ui_group {
  &:hover {
    .ui_group-hover\:block {
      display: block;
    }
  }
}

.ui_linear_mask_top {
  background-image: linear-gradient(to bottom, rgba(0, 0, 0, 0.4), transparent);
}

.ui_linear_mask_bottom {
  background-image: linear-gradient(to top, rgba(0, 0, 0, 0.4), transparent);
}

.ui_theme_bg_color {
  background-color: var(--themeColor) !important;
}

.hover\:ui_themeColor:hover {
  color: var(--themeColor);
}

.ui_bg_opacity {
  position: relative;
  overflow: hidden;

  &::before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: currentColor;
    opacity: var(--themeOpacity);
  }
}

.ui_w1100 {
  max-width: 1100px;
  margin: 0 auto;
}

.ui_ellipse {
  display: -webkit-box;
  /*! autoprefixer: ignore next */
  -webkit-box-orient: vertical;
  -webkit-line-clamp: 2;
  overflow: hidden;
}

.ui_underline {
  position: relative;
  &::after {
    content: '';
    position: absolute;
    left: 0.3em;
    right: 0.3em;
    bottom: -0.3em;
    height: 0.15em;
    background-color: var(--themeColor);
  }

}

.ui_link {
  color: var(--linkColor);

  &:hover {
    color: #5E58C1;
  }
}

.ui_filter {
  .row {
    .list {
      display: flex;

      .item {
        width: 80px;
      }
    }
  }
}

.ui_headerBar {
  flex: none;
  display: flex;
  padding: 10px 20px;
  padding-left: 30px;
  align-items: baseline;

  .left {
    display: flex;
  }

  .right {
    margin-left: auto;
  }

  .search {
    position: relative;
    width: 200px;
    height: 24px;

    input {
      padding: 10px;
      width: 100%;
      height: 100%;
      border: none;
      border-radius: 1000px;
      background-color: #F7F7F7;
    }

    .ico {
      position: absolute;
      top: 50%;
      transform: translate(0, -50%);
      right: 10px;
    }
  }
}


.ui_process {
  --ratio: 0;
  display: inline-block;
  width: 110px;
  height: 10px;
  border-radius: 5px;
  background-color: #E8E8EA;
  overflow: hidden;

  &::before {
    content: '';
    display: block;
    height: 100%;
    width: calc(var(--ratio) * 100%);
    // min-width: 1%;
    background-color: #507DAF;
  }
}

.ui_header {
  flex: none;
  height: 60px;
  padding: 0 30px;
  display: flex;
  align-items: center;
}

.ui_dialog {
  width: min-content;
  height: min-content;
  z-index: 20;

  &_close {
    position: absolute;
    top: 10px;
    right: 10px;
    font-size: 36px;
    z-index: 2;
    color: #999;

  }

  &_header {
    height: 70px;
    cursor: move;
  }

  &_title {
    font-size: 16px;
    font-weight: bold;
  }
}

.ui_threes_ico {
  width: calc(var(--size) * 1px);
  height: calc(var(--size) * 1px);
  padding: calc(var(--size, 4) / 5 * 1px);
  color: var(--logoColor);
  border-radius: 1000px;

  display: inline-flex;

  &.hover {
    border: 1px solid #ccc;
  }

  &.active,
  &.hover:hover {
    color: #fff;
    background-color: var(--logoColor);
    border-color: var(--logoColor);
  }

  &.wyy {
    --logoColor: #EC4141;
  }

  &.sx {
    --logoColor: #5BBAEB;
  }

  &.wx {
    --logoColor: #67B633;
  }

  &.qq {
    --logoColor: #34A0DF;
  }

  &.qz {
    --logoColor: #F7CB41;
  }

  &.wb {
    --logoColor: #EA4848;
  }

  &.wy {
    --logoColor: #D33A31;
  }

  &.lj {
    --logoColor: #A9B6C5;
  }

  &.phone {
    --logoColor: #E7A84F;
  }
}
