:root {
  --themeOpacity: 0.1;
  --themeRed: #D03535;
  --themeBlue: skyblue;
  --lrcColor: var(--themeBlue);
  --linkColor: #507DAF;
  --themeColor: var(--themeRed);
  --themeOpacityColor: rgba(var(--themeColor), var(--themeOpacity));
  --fontFamily: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen,
    Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
}

