.h1 {
  font-weight: bold;
  font-size: 20px;
}

.commonpage {
  display: flex;
  justify-content: center;

  .btn {
    margin: 0 4px;
    display: flex;
    align-items: center;
    justify-content: center;
    width: 20px;
    height: 20px;
    border-radius: 4px;
    border: 1px solid #E6E6E6;

    &:hover {
      background-color: #F4F4F4;
    }

    &:not(.disabled) {
      cursor: pointer;
    }

    &.on {
      background-color: var(--themeColor);
      color: #fff;
      border-color: var(--themeColor);
    }

    .disabled {
      color: #E6E6E6;
    }
  }
}

/*滚动条样式*/
::-webkit-scrollbar {
  width: 6px;
  height: 4px;
}

::-webkit-scrollbar-thumb {
  border-radius: 10px;
  background-color: #E1E1E1;
}

::-webkit-scrollbar-track-piece {
  background-color: #fff; // 底
}

::-webkit-input-placeholder {
  /* Chrome/Opera/Safari */
  color: #B6B6B6;
}

.flex-center {
  display: flex;
  align-items: center;
  justify-content: center;
}

.content-visibility-auto {
  content-visibility: auto;
}

@import 'ui';
